  '"2018年5月11日分支机构柜员为客户办理业务，查询客户信息时，系统出现以下提示内容: 客户风险等级：积极型 客户风险等级到期日期：20171231 该客户风险等级已到 以下说法正确的是（）",客户办理分级基金权限开通前需进行风险测评,客户办理身份证件有效期更新前无需进行风险测评,,,,',
