  '个人客户代理人不得办理（）业务。,重要信息修改,转授权,,,,',
  '客户首次办理开放式基金跨市场转托管业务前，需先通过分支机构申请建立证券账户与基金账户对应关系，通过BOP 系统（）菜单进行对应关系建立。办理上市开放式基金跨市场转托管，登录UF系统（）菜单输入客户基金账户对应的场内证券账户所在席位号，输入份额应为整数，小数点后份额不得办理跨市场转托管。,中登业务--证券业务--证券基金账户关系维护,产品-证券理财业务-证券理财转托管,,,,',
