  '通过上海证券交易所固定收益平台进行申报的现券交易包括（）,指定对手方报价,债券换股申报,债券回售申报,"注意：现券交易是abc,若是只问交易类型是ABCD",,',
  '上海证券交易所固定收益平台业务，客户办理指定对手方买入报价，分支机构经办必须冻结的资金包括：（）,买入债券的金额（按照净价计算）,买入债券的应计利息,佣金费用,,,',
