  '质权人为机构办理质押登记业务时,出具的授权委托书应具备以下()要素。,列明授权双方的姓名、身份证件类型及号码,列明授权办理的事项。,列明委托期限,,,',
  '以下说法正确的有（）,新债上市当天，客户卖出委托废单提示“产品状态资源访问权限不足”，一般情况下是该产品停牌了,客户开户后委托了深圳的股票，显示废单，原因为“账户是未开户账户”，是因为该深圳证券账户为当天新开证券账户。,客户在营业部开户且上海A股证券账户指定成功，但委托回报废单提示“账号已被挂起”。一般是该证券账户已被中登注销。,,,',
  '分支机构在为客户办理证券账户的关键信息变更、休眠账户激活等业务前，应当先进行客户账户关联关系的检查，涉及（）、（）、（）等与客户权益业务直接相关的业务应当由经办操作、复核员复核。,账户开户,信息修改,账户注销,,,',
  '分支机构柜员登陆系统进行红冲蓝补操作时，需向复核员提交以下（）必要材料，以便对照审核。（）,签批完整的《资金账户红冲蓝补申请表》,审批完整的红冲兰补OA请示,总部发送的统一调整通知,,,',
  '经办为客户办理开通深圳账户快速交易通道业务，有以下哪些注意事项（）,客户有深圳证券持仓的，须先办理转托管。,经办检查确认当日客户深圳A股证券账户无委托交易记录，如有委托交易不得办理转托管及后续操作。,若客户无深圳证券持仓，经办仅需登录系统菜单，输入客户编号/资产账号，申报级别选择“0普通”，修改深圳证券账户席位为057600,,,',
  '个人客户开通以下权限有申请前二十个交易日日均资产要求，其中（）权限的日均资产指证券类资产。（）,分级基金,退市整理股票,沪港通,,,',
  '关于申购新股以下说法正确的是（）,客户同一个证券账户在多家券商托管的，市值合并计算,客户有多个证券账户的，多个证券账户市值合并计算,客户在多家券商有沪A账户，且沪市市值合并计算后满足申购新股的要求，客户可以在其中一个无沪市股票持仓的券商申购新股,,,',
  '关于经纪业务印章的用途的表述，正确的有：（）,中国结算证券账户开户专用章仅用于证券账户开立、注销、账户注册资料修改等业务,信用证券账户开户专用章仅用于信用证券账户开户及登记业务,柜台业务清讫章可用于上级主管部门、有权机关需立即提供的非资产证明类材料,,,',
  '下列（）业务需要在交易日15:00前发起并由复核员审核通过。,两网及退市公司股票权限开通；,股转一类合格投资者权限开通,一站式销户。,,,',
  '以下属于A类专业投资者的有（）。,社会保障基金,企业年金等养老基金,保险公司发行的保险产品,,,',
  '机构客户开通退市整理股票交易权限以下说法错误的是：（）,A、申请开通权限前 20 个交易日日均证券类资产不得低 于人民币 50 万元。,B、具备两年以上股票交易经验,D、申请开通权限前 20 个交易日日均证券类资产不得低 于人民币 30万元。,,,',
  '以下（）属于普通账户通办业务。,账户密码重置,证券理财产品赎回,资产账户销户,,,',
  '客户从其他券商撤指后，T日在营业部开户并指定成功，T+1日买入上海股票，委托回报废单，原因是“账号已被挂起”。请问是什么原因？（）,客户上海账户在T日被A券商进行了中登注销,由于上海指定信息是T日当晚清算由上交所交互给中国结算，T日日间A券商办理中登注销时，中国结算统一账户平台判定客户上海账户仍指定在A券商且无证券余额，所以中登注销成功,对于账户被挂起，客户需临柜注销该上海账户，重新新开上海账户并指定,,,',
  '分支机构受理投资者证券质押登记业务申请前，应先通过 OA 请示向零售业务条线事业部申请代理证券质押登记业务资格，申请内容包括：（）,"分支机构拟代理证券质押登记业务的投资者信息、涉及',
  '的证券名称、代码和数量；",分支机构相关业务人员参加代理证券质押登记业务培训并通过考试、业务办理设备、资料保管情况的说明；,公司要求提供的其他资料,,,',
  '经办将大宗交易申报单与系统客户账户信息进行核对包括 (    ),资金账户上是否有足额资金,证券账户是否有足额证券,同一笔大宗交易买卖双方的约定号必须相同，股份类别（即受限股份或非受限股份）须一致。不同笔大宗交易的约定号不得相同。,,,',
  '上市公司的以下人员（  ）通过大宗交易减持股份的，须明确买卖股份是否属于受减持规定限制的股份。如属于受减持规定限制的股份的，分支机构应向客户履行提醒、告知和督促义务。,大股东 ,特定股东,董监高,,,',
  '关于机构开户预留的印鉴卡，下列说法正确的是（）。,印鉴卡正面预留印模加盖机构公章、法定代表人私章,如为合伙企业，则在印鉴卡正面加盖执行事务合伙人私章或委派代表私章,印鉴卡背面加盖机构公章，“提送” 处打钩，或划去“更换”,,,',
  '以下柜台业务说法正确的有（）,新开深圳证券账户当天不能进行股票委托,休眠激活账户，当天登记存管的，账户资金次日才能转出至银行卡，对所有银行都适用,"客户深圳持仓股份',
