  '融资融券账户迁移需变更邮箱所属部门，需按以下顺序操作（）。,发起迁移业务方应及时通知对方营业部配合进行追保邮箱部门修改设置,迁出方通过263管理平台将客户的追保邮箱从本部门移至未分配部门,迁入方通过263管理平台将追保邮箱从未分配部门移入本部门,,,',
  '关于信用客户开户以下说法正确的是？（）,22周岁（含）以下客户需签署《特殊人群风险警示书》,所有客户开户均需签署《回访表》，回访日期同开户成功日期一致。,70周岁（含）以上客户需签署《特殊人群风险警示书》，还需其直系亲属加签《担保书》，留存身份证复印件。,,,',
  '以下哪种类型客户不能开立信用账户（）,客户的普通沪A股东卡状态为“新指定”,查出法院有失信记录,客户在其他证券公司已开信用账户,,,',
  '分支机构协助有权机关对信用账户操作前应（),核验执行人证件、介绍信或法律文书的真实有效性,先行上报信用业务管理总部，在征得同意后，方可进行下一步操作,分支机构检查客户信用账户内有无负债。如信用账户内有负债的，应将执行的相关材料发给信用业务管理总部风控岗。,,,',
  '首次授信额度超过1000万元（含）的客户应（）,填写《业务内部操作审批单》,《业务内部操作审批单》由营业部内部审批。,客户的信用账户和普通账户均需设置资金转出限制,,,',
  '有权机关要求办理账户控制业务时提供的材料包括以下()：,执行人的工作证或执行公务证及复印件,协助解冻执行通知书,裁定书,,,',
  '分支机构协助法院执行扣划时，经检查发现客户开立了信用账户，信用账户内无负债，但有担保证券。柜员要如何处理？（）（按操作先后顺序回答）,上报信用部风控岗征得同意后，设置信用资产账户“禁止买入，禁止银行支取，禁止柜台支取”。,将剩余证券划转到客户普通证券账户。,确定信用账户内无担保证券后对信用账户设置“禁止担保品划转”。,注意：容易选错成ABC，原题库答案不对,,',
  '在引进公募基金券商结算产品工作中，分支机构主要职责有（）,负责与基金管理人签署《证券经纪服务协议》《外部交易系统接入协议》和《专用交易单元使用协议》等协议。,负责对基金管理人进行尽职调查，了解和核实管理人及其交易系统等有关信息,办理公募基金经纪账户开销户,,,',
  '以下说法不正确的有（）,个人客户代理人不得办理修改客户本人姓名、身份证升位等账户重要信息修改业务,机构客户代理人提供的法人授权委托书必须加盖机构公章或法定代表人私章,某个人客户代理人手持经国家公证机关公证的客户授权委托书临柜代客户办理开立客户号业务，经办以代理人不得代办开立个人客户号业务为由予以拒绝,,,',
  '客户因有曾用名，临柜办理姓名修改，但其名下证券账户关联关系为“未确认”，因涉及一码通账户信息变更，以下（）证券账户需要申报关联关系确认。（）,封闭式基金账户（配号除外）；,上海A股证券账户；,上海B股证券账户。,,,',
  '对于以下（）业务，经办必须核对客户是否属于首次在上证固收平台申请。属于首次申请的，相关证券账户需提前在上证固收平台进行维护后方可申报。,债券换股,债券回售,债券质押式协议回购,,,',
  '客户申报债券质押式协议回购业务，经办核对客户资金账户上是否有足额资金或证券账户上是否有足额证券，下列说法正确的有（）,客户办理债券质押式协议回购逆回购的，分支机构经办必须冻结的账户金额为客户借出金额和佣金费用,客户债券质押式协议回购正回购到期续做的，分支机构经办必须确保已足额冻结到期结算利息,客户申报指定对手方卖出报价、债券质押式协议回购正回购的，分支机构经办应事先核查客户申报的债券可用数量是否足额,,,',
