  '关于大额取款预约管理细则，说法正确的是（  ）,客户的衍生品资金账户及港币资金账户大额取款预约应至少提前一个工作日办理；,客户美元资金账户大额取款预约应不迟取款日的前一个工作日13:50办理。,当前公司对客户的普通资金账户外币银证转账设置美元5千元和港币5万元的预约取款金额标准。,,,',
  '以下（）资金红冲蓝补事项OA审批需经过运营管理部？（）,100万元以上的客户移民身份变更总部代划付请示,50万元以下的协助司法扣划请示,因佣金管理、利率管理等非常规事项发起的红冲蓝补请示,,,',
  '对于申请开通股转系统一类合格投资者交易权限的个人客户，经办应核查客户符合以下条件：（）,具有2年以上证券、基金、期货投资经历，或者具有2年以上金融产品设计、投资、风险管理及相关工作经历,新三板知识测试成绩不得低于80分,“证券期货市场失信记录查询”不属于禁止参与股票交易的,存在一题答案是ACD需认真核对答案顺序,,',
