  '关于资金红冲蓝补，下列说法正确的是（）,存管红冲蓝补的金额晚上发送银行，当日蓝补金额无法取出；,建行存管客户结息当日，利息不得办理红冲，只能红冲本金。次日重新结息后进行红冲。,涉及遗产继承发生的资金红冲业务，若继承人属于境内完全民事行为人，可以采取将被继承人账户红冲的资金相应蓝补至继承人账户的方式。,,,',
