  '协助有权机关办理查询业务，有权机关包括以下（）单位。,中国人民银行,人民法院,监狱保卫部门,国家安全部门,,',
  '客户的重要信息修改包括：（）,个人客户修改姓名,个人客户修改证件类型或身份证改号,机构客户修改机构全称,机构客户修改机构证件号码、机构法定代表人,,',
  '客户办理重要业务时，业务操作人员必须为其采集留存客户头像资料，重要业务包括（）但不限于。,开户业务；开通新业务权限,清空客户资金密码；清空客户交易密码,第三方存管预指定或指定存管银行；撤销指定交易、转托管,"客户重要信息修改（修改账户名称或身份证件号码，身份证自然升位除外）",,',
  '客户账户存在下列（）情况时，分支机构不能为客户办理上海撤销指定交易业务。,沪市当日有委托申报（无论是否成交）,账户处于挂失、冻结或申请非交易过户期内,前一交易申购了沪市新股,沪市账户中有债券回购或其他事项未了结,,',
