  '经办为信用账户客户办理遗产继承，需按以下步骤处理：（）,继承人在分支机构开立客户号并登记三方存管，应注意开立的账户不得为纯资金账户。,红冲被继承人信用资产账户资金并经审核后，蓝补相应金额至其普通资产账户并经审核；,红冲被继承人普通资产账户资金并经审核，蓝补相应金额至继承人普通资产账户并经审核。,以上资金红冲和蓝补的操作及审核可以在同一天完成。,,',
